.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    padding: 10px 20px;
    color: white;
  }
  
  .navbar-left h3 {
    margin: 0;
  }
  
  .navbar-right button {
    padding: 8px 16px;
    background-color: orange;
    border: none;
    color: white;
    cursor: pointer;
    border-radius: 4px;
  }
  
  .navbar-right button:hover {
    background-color: darkorange;
  }
  